@import '../../assets/styles/variables';

.section{
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  position: relative;
  overflow: hidden;

  &_inner{
    padding: 120px 15px 0;
    margin: 0 auto;
    max-width: 80rem;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    gap: 1.25rem;
  }
}

.computerCanvas{
  display: flex;
  justify-content: center;
  align-items: end;
  position: absolute;
  margin: 0 auto;
  width: 100%;
  height: 100%;
  bottom: 5%;
  left: 0;
  right: 0;
}

.computerImg{
  display: none;
}


.dec{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 1.25rem;

  &_circle{
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: $main;
  }

  &_line{
    margin: 0 auto;
    width: 5px;
    height: 400px;
    background: linear-gradient(to bottom, $main, rgba(0, 0, 0, 0));
    
  }
}

.text{
  margin-top: 25px;
  position: relative;
  z-index: 10;


  h1{
    transition: opacity .1s;
    line-height: 98px;
    font-size: 80px;
    font-weight: 900;
    margin-top: 0.5rem;
  }

  span{
    color: $main
  }
}

.subtext{
  color: $secondary;
  line-height: 40px;
  font-size: 22px;
  font-weight: 500;



  div{
    padding-right: 25px;
    position: relative;
    transition: all .1s;


    &:nth-child(2){
      padding-left: 55px;

      &::after{
        position: absolute;
        content: "";
        left: 0%;
        top: 50%;
        transform: translateY(-50%);
        width: 40px;
        height: 10px;
        background-color: white;
      }
  
    }
  }
}

.wrapper_btnDown{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 2.5rem;
}



@media (max-width: 1300px) {
  .text{
    h1{
      font-size: 65px;
      line-height: 70px;
    }
  }
  .subtext{
    font-size: 22px;
    line-height: 38px;
  }
}

@media (max-width: 1040px) {

  .text{
    h1{
      font-size: 60px;
      line-height: 68px;
    }
  }

  .subtext{
    font-size: 20px;
    line-height: 38px;

    div{
      &:nth-child(2){
        padding-left: 0; 

        &::after{
          display: none;
        }
      }
    }
  }

}

@media (max-width: 980px) {
  .text{
    h1{
      margin-bottom: 10px;
      font-size: 46px;
      line-height: 60px;
    }
  }
  .subtext{
    font-size: 22px;
    line-height: 36px;
  }
}


@media (max-width: 768px) {


  .computerCanvas{
    display: none;
  }

  .computerImg{
    display: block;
    animation: rotate 4s ease-in-out infinite alternate;
    
  }

  .text{
    h1{
      font-size: 36px;
      line-height: 40px;
    }
  }

  .subtext{
    margin-bottom: 75px;
  }
}

@media (max-width: 600px) {
  .text{
    h1{
      font-size: 28px;
      line-height: 40px;
    }
  }
  .subtext{
    font-size: 16px;
    line-height: 30px;
  }
}

@media (max-width: 475px) {
  .section{

    margin-bottom: 115px;
    height: auto;
  }


  .text{
    h1{
      font-size: 22px;
      line-height: 40px;
    }
  }

  .subtext{
    font-size: 14px;
    line-height: 28px;
  }

}


@media (max-width: 400px) {

  .section{
    &_inner{
      padding: 100px 5px 0px 15px;
    }
  }
  
  .text{
    h1{
      font-size: 20px;
      line-height: 26px;
    }
  }

  .subtext{
    font-size: 14px;
    line-height: 24px;
  }

}

@media (max-width: 340px) {
  .text{
    h1{
      font-size: 18px;
      line-height: 26px;
    }
  }
}

@media (max-height: 667px) {
  .section{
    margin-bottom: 115px;
    height: auto;
  }

  .computerCanvas{
    display: none;
  }
}



@keyframes rotate{
  0%{
    transform: translateY(0);
  }
  50%{
    transform: translateY(-15px);
  }
  100%{
    transform: translateY(15px);
  }
}









