@import '../../assets/styles/variables';
@import '../../assets/styles/mixins';

.title{
  @include section-title;
}

.subtitle{
  @include section-subtitle;
}


.subtext{
  max-width: 48rem;
  min-height: 144px;
  color: $light;
  line-height: 30px;
  font-size: 18px;
};

.items{
  padding-bottom: 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 5rem;
  gap: 2.5rem;
  margin-bottom: 120px;
}

.item{
  width: 175px;
  height: 175px;

  &_img{
    width: 100px;
    height: 100px;
  }
}

.card{
  cursor: pointer;
  position: relative;
  width: 200px;
  min-height: 240px;
  height: 100%;
  padding: 1px;
  border-radius: 20px;
  @include gradient-dark;

  &_wrapper{
    width: 100%;
    height: 100%;
    background-color: $darkMain;
    border-radius: 20px;
  }

  &_inner{
    border-radius: 20px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    height: 100%;
    padding-top: 1.25rem;
    padding-bottom: 1.25rem;
    transition: opacity .5s;
  }

  &_inner_hover{
    position: absolute;
    top: 0px;
    width: 99%;
    height: 99%;

    text-align: center;

    border-radius: 20px;
    padding: 20px;
   
    opacity: 0;
    transition: opacity .5s;
  }

  &_icon{
    width: 75px;
    height: 75px;
    object-fit: contain;
  }

  &_text{
    font-size: 20px;
  }

  &_backtext{
    background: linear-gradient(-90.13deg, saturate(lighten(#00cea8, 20%), 100%) 1.9%, saturate(lighten(#ffffff, 20%), 100%) 97.5%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  &_hovered{
    &:hover{
      .card_inner{
        opacity: 0;
      }
  
      .card_inner_hover{
        opacity: 1;
      }
    }
  }
}


@media (max-width: 1280px){
  .items{
    padding-bottom: 100px;
  }
}


@media (max-width: 960px){
  .subtext{
    max-width: 100%;
  }

  .items{
    margin-top: 25px;
    gap: 0;
    justify-content: center;
  }
}



@media (max-width: 768px){
  .subtext{
    margin-top: 20px;
  }
  .item{
    width: 100px;
    height: 100px;
    margin: 10px 20px;
  }
}

@media (max-width: 500px){
  .subtext{
    font-size: 16px;
    line-height: 26px;
  }

}


@media (max-width: 450px){
  .items{
    margin-bottom: 50px;
  }

  .item{
    width: 80px;
    height: 80px;
    margin: 7.5px 10px;

    &_img{
      width: 80px;
      height: 80px;
    }
  }
}


@media (max-width: 400px){
  .title{
    font-size: 64px;
  }

  .subtitle{
    font-size: 26px;
  }

  .subtext{
    margin-top: 0;
    font-size: 14px;
    line-height: 24px;
  }

  .items{
    canvas{
      width: 100px;
      height: 100px;
    }
  }

  .item{
    width: 70px;
    height: 70px;
 
    &_img{
      width: 70px;
      height: 70px;
    }
  }
}





