@import '../../assets/styles/variables';
@import '../../assets/styles/mixins';


.title{
  @include section-title;
}

.subtitle{
  @include section-subtitle;
}

.section{
  padding: 25px 0 75px;
  display: flex;
  overflow: hidden;  

  &_inner{
    width: 50%;
    margin-right: 50px;
    border-radius: 16px;
    padding: 24px 32px;
    background-color: $darkMain;
  }

  &_earth{
    width: 50%;
  }
}

.form{
  display: flex;
  flex-direction: column;
  gap: 32px; 
  margin-top: 48px;

  textarea{
    resize: none;
  }
}

.label{
  display: flex;
  flex-direction: column;

  &_text{
    font-weight: 500;
    margin-bottom: 16px;
  }
}

.input{
  background-color: #151030;
  padding: 16px 24px;
  border-radius: 8px;
  border-style: none;
}

.btn[type="submit"]{
  width: fit-content;
  font-weight: 700;
  padding: 1px;
  outline: none;
  border-radius: 12px;
  @include gradient-dark;
  --tw-shadow-color: #050816;
  --tw-shadow: var(--tw-shadow-colored);
  --tw-shadow-colored: 0 8px 12px 2px var(--tw-shadow-color), 0 2px 4px -2px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

.btn_inner{
  border-radius: 12px;
  background-color: $darkMain;
  padding: 12px 32px;
  transition: background-color .4s;
}

.btn:hover{
  .btn_inner{
    background-color: transparent;
  }
}

@media(max-width: 1100px){
  .title{
    font-size: 70px;
  }
}


@media(max-width: 990px){
  .section{
    padding: 0;
    padding-bottom: 125px;
    flex-direction: column-reverse;

    &_inner{
      width: 100%;
    }

    &_earth{
      width: 100%;
      height: 600px;
    }
  }
}


@media(max-width: 768px){
  .section{
    &_earthImg{
      margin: 0 auto 75px;
      width: 25rem;

      animation: transform 4s ease-in-out infinite alternate;
    }
  }
}

@media (max-width: 460px){
  .title{
    text-align: center;
    font-size: 46px;
  }

  .subtitle{
    text-align: center;
    font-size: 20px;
  }

  .input{
    padding: 8px 12px;
    font-size: 14px;
    &::placeholder{
      font-size: 14px;
    }
  }

  .btn{
    margin: 0 auto;
  }

  .form{
    margin-top: 24px;
  }
}


@keyframes transform{
  0%{
    transform: translateY(-15px);
  }
  50%{
    transform: translateY(0px);
  }
  100%{
    transform: translateY(15px);
  }
}