@import '../../assets/styles/variables';
@import '../../assets/styles/mixins';

.section{
  position: relative;
  overflow-x: hidden;
}

.header{
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.title,
.subtitle{
  text-align: end;
}

.title{
  @include section-title;
}

.subtitle{
  @include section-subtitle;
}

.text{
  @include text;
}

.tabs{
  max-width: max-content;

  &_inner{
    display: flex;
  }
}

.tab{
  width: 100%;
  min-width: 175px;
  font-weight: 600;
  cursor: pointer;
  padding: 1px;
  padding-right: 0;
  font-size: 20px;
  overflow: hidden;
  @include gradient-dark;
  transition: all .4s;

  
  &_inner{
    position: relative;
    text-align: center;
    padding: 10px 0;
    background-color: $darkMain;


    &::after{
      position: absolute;
      margin: 0 auto;
      content: '';
      width: 0%;
      height: 100%;
      top: 0;
      left: 0;
      right: 0;
      z-index: 0;
      @include gradient-dark;
      transition: all .2s;
    }

    span{
      position: relative;
      z-index: 1;
    }
  }

  &:nth-child(1){
    border-bottom-left-radius: 20px;
    border-top-left-radius: 20px;
  }

  &:nth-child(1) .tab_inner{
    border-bottom-left-radius: 20px;
    border-top-left-radius: 20px;
  }

  &:nth-last-child(1){
    border-bottom-right-radius: 20px;
    border-top-right-radius: 20px;
    padding-right: 1px;

  }

  &:nth-last-child(1) .tab_inner{
    border-bottom-right-radius: 20px;
    border-top-right-radius: 20px;
  }



  &:hover:not(.tab_active) .tab_inner{
    &::after{
      width: 100%;
    }
  }

  &:hover.tab_active{
    cursor: default;
  }

  &_active{
    min-width: 225px;
  }

  &_active .tab_inner{
    @include gradient-dark;
  }
}

.swiper_head{
  margin: 75px 0 25px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.swiper_btns{
  display: flex;
}

.swiper_btn{
  padding: 1px;
  cursor: pointer;
  margin: 0 5px;
  border-radius: 5px;
  @include gradient-dark;
  transition: opacity .4s;

  &_inner{
    display: flex;
    justify-content: center;
    align-self: center;
    border-radius: 5px;
    background-color: $darkMain;
  }

  &_img{
    width: 50px;
    height: 50px;
    border-radius: 5px;
  }



  &:first-child{
    .swiper_btn_img{
      transform: rotate(-180deg);
    }
  }

  &_disabled{
    cursor: default;
    .swiper_btn_inner{
      @include gradient-dark;
    }
  }
  
  &:hover:not(.swiper_btn_disabled){
    opacity: .7;
  }
}

.btnDown{
  margin: 0 auto;
  position: absolute;
  left:0;
  right: 0;
  bottom: 40px;
}

@media(max-width: 1160px){
  .text{
    max-width: 50%;
  }
}

@media(max-width: 800px){
  .header{
    flex-direction: column-reverse;

    div{
      width: 100%;
    }
  }

  .subtitle{
    text-align: start;
    width: 100%;
  }
  .title{
    text-align: start;
    margin-bottom: 25px;
  }

  .text{
    max-width: 100%;
  }
}

@media (max-width: 768px){
  .swiper{
    &_btns{
      padding: 0 15px;
      margin: 0 auto;
      z-index: 10;
      width: 100%;
      display: flex;
      justify-content: space-between;
      position: absolute;
      justify-content: center;
      bottom: 10%;
    }

    &_btn{
      margin: 0 10px;
    }

    &_head{
      justify-content: center;
    }
  }
}

@media(max-width: 600px){
  .tab{
    font-size: 18px;
    min-width: 125px;

    &_active{
      min-width: 150px;
    }
  }
}


@media (max-width: 500px){
  .text{
    font-size: 16px;
    line-height: 26px;
  }

}


@media (max-width: 480px){
  .tab{
    min-width: 110px;
  }

  .swiper{
    &_btns{
      padding: 0 5px;
    }
  }
}



@media (max-width: 400px){
  .tab{
    min-width: 100px;
  }


  .title{
    margin-bottom: 0;
    font-size: 64px;
  }

  .subtitle{
    font-size: 26px;
  }

  .text{
    margin-top: 0;
    font-size: 14px;
    line-height: 24px;
  }
}






