.swiper{
  padding: 30px 25px;
  margin-bottom: 175px;

}

@media (max-width: 1400px){
  .swiper{
    width: 100%;
    max-width: 1100px;

  }
}


@media (max-width: 960px){
  .swiper{
    width: 100%;
    max-width: 700px;

  }
}

@media (max-width: 540px){
  .swiper{
    width: 100%;
    max-width: 300px;
  }
}





