@import '../../assets/styles/variables';
@import '../../assets/styles/mixins';

.navigatePanel{
  display: none;
  margin: 0 auto;
  position: fixed;
  z-index: 100;
  bottom: 3%;
  right: 0;
  left: 0;
  width: max-content;
  height: 65px;

  padding: 1px;
  border-radius: 10px;
  background-color: rgba($darkMain, .9);

  &_inner{
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
  }

  &_item{
    cursor: pointer;
    padding: 1px;

    margin: 0 8px;
    width: 50px;
    height: 50px;
    border-radius: 5px;
   
    &_inner{
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;

      border-radius: 5px;      
    }



    &_img{
      width: 25px;
      height: 25px;
    }

    &_active{
      cursor: default;
      @include gradient-dark;

      .navigatePanel_item_inner{
        background-color: #000;
      }
    }

    &:hover:not(.navigationPanel_item_active){
      @include gradient-dark;

      .navigatePanel_item_inner{
        background-color: #000;
      }
    }
  }
}

@media (max-width: 1280px){
  .navigatePanel{
    display: block;
  }
}