@import '../../assets/styles/variables';
@import '../../assets/styles/mixins';

.navbar{
  position: fixed;
  padding: 1.25rem 0;
  width: 100%;
  z-index: 20;
  top: 0;
  background-color: #050816;
  box-shadow: 4px 8px 16px rgba(0, 0, 0, 0.5);

  &_inner{
    margin: 0 auto;
    max-width: 80rem;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &_logo{
    cursor: pointer;
    gap: 0.5rem;
    display: flex;
    align-items: center;
  }

  &_logo_img{
    object-fit: contain;
    width: 3rem;
    height: 3rem;
  }

  &_links{
    display: flex;
    align-items: center;
    flex-direction: row;
    list-style: none;
  }

  &_link{
    display: flex;
    align-items: center;
    gap: 10px;
    margin: 0 1.5rem;
    transition: color .1s;
    color: #fff;
    cursor: pointer;
  
    &:hover{
      color: $main;
    }
  
    &_active{
      color: $main;
    }
  }

  &_socials{
    display: none;
  }

  &_social{
    margin-right: 15px;
    padding: 1px;
    width: 50px;
    height: 50px;
    @include gradient-dark;
    border-radius: 50%;

    &_link{
      position: relative;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: $darkMain;
      border-radius: 50%;
      transition: background-color .3s;
    }

    &_img{
      position: absolute;
      width: 25px;
      height: 25px; 
    }

    &:nth-child(1){
      .navbar_social_img{
        right: 12px;
      }
    }

    &:hover{
      .navbar_social_link{
        background-color: transparent;
      }
    }
  }
  
}


.text{
  display: flex;
  align-items: center;
  transition: all .4s;
  span{
    margin-left: 2.5px;
    display: inline-flex;
    align-items: center;
    color: $main;

    img{
      margin-left: 5px;
      width: 30px;
    }
  }

  &:hover{
    color: $main
  }
}



@media (max-width: 1300px) {
  .navbar{
    &_inner{
      padding: 0 15px;
    }
  }

}

@media (max-width: 1280px) {
  .navbar{
    &_links{
      display: none;
    }

    &_socials{
      display: flex;
    }
  }

}

@media (max-width: 600px) {
  .text{
    font-size: 14px;

    span{
      img{
        width: 20px;
      }
    }
  }

  .navbar{
    padding: 1rem 0.25rem;

    &_logo_img{
      width: 2.5rem;
      height: 2.5rem;
    }

    &_social{
      width: 40px;
      height: 40px;

      &_img{
        width: 20px;
        height: 20px;
      }

      &:nth-child(1){
        .navbar_social_img{
          right: 10px;
        }
      }
    }
  }
}

@media (max-width: 490px) {

  .navbar{
    &_inner{
      padding: 0;
    }

  

    &_social{
      margin-right: 7.5px;
    }
  }
}

@media (max-width: 440px) {

  .text{
    span{
      span{
        display: none;
      }
    }
  }
}





