@mixin section-title(){
  font-size: 78px;
  font-weight: 600;
  line-height: 80px; 
}

@mixin section-subtitle(){
  color: $light;
  font-size: 30px;
}

@mixin gradient(){
  background: -webkit-linear-gradient(-90.13deg, #00cea8 1.9%, #bf61ff 97.5%);
  --tw-shadow: 0px 35px 120px -15px #211e35;
  --tw-shadow-colored: 0px 35px 120px -15px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

@mixin gradient-dark(){
  background: -webkit-linear-gradient(-90.13deg, darken(#00cea8, 10%) 1.9%, darken(#bf61ff, 10%) 97.5%);
  --tw-shadow: 0px 35px 120px -15px #211e35;
  --tw-shadow-colored: 0px 35px 120px -15px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

@mixin text(){
  max-width: 48rem;
  min-height: 120px;
  color: #AAA6C3;
  line-height: 30px;
  font-size: 18px;
}

