@import '../../../assets/styles/variables';
@import '../../../assets/styles/mixins';


.items{
  margin-top: 25px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.item{
  position: relative;
  height: 380px;
  padding: 1px;
  border-radius: 20px;
  @include gradient-dark;
  box-shadow: none;

  &_inner{
    display: flex;
    flex-direction: column;
    padding: 10px;
    border-radius: 20px;
    height: 100%;
    background-color: $darkMain;
    border-radius: 20px;
  }

 
  &_text{
    font-size: 16px;
    line-height: 18px;
  }

  &_descr{
    font-size: 14px;
    line-height: 18px;
  }

  &_img{
    width: 100%;
    height: 200px;
    border-radius: 20px;
    margin-bottom: 15px;
  }

  &_btns{
    display: flex;
    justify-content: center;
    margin-top: auto;
  }

  &_btn_inner{
    background-color: transparent;
    padding: 7.5px 10px;
    border-radius: 5px;
 
  }

  
  &_btn, &_btn_secondary{
    cursor: pointer;
    margin: 0 7.5px;
    padding: 1px;    
    border-radius: 5px;
    @include gradient-dark;
    transition: all .4s;
  
  }

  &_btn .item_btn_inner{
    background-color: $darkMain;
  }

  &_btn:hover{
    transform: scale(1.1);
  }

  &_btn_secondary:hover{
    transform: scale(1.1);
  }
}

@media(max-width: 640px){
  .item{
    height: 320px;

    &_text{
      font-size: 12px;
      max-height: 70px;
    }

    &_img{
      height: 150px;
      margin-bottom: 10px;
    }

    &_btn_inner{
      font-size: 14px;
      padding: 5px 7.5px;
    }

  }
}




