@import '../../assets/styles/variables';
@import '../../assets/styles/mixins';


.contactBtn{
  cursor: pointer;

  position: fixed;
  bottom: 50px;
  right: 50px;
  z-index: 10;
  width: 75px;
  height: 75px;
  padding: 2px;

  border-radius: 50%;
  @include gradient-dark;

  &_inner{
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;

    background-color: $darkMain;
    border-radius: 50%;

    transition: background-color .4s;
  }

  
  &_img{
    width: 50px;
    height: 50px;
  }



  &:hover{

    .contactBtn_inner{
      @include gradient-dark
    }


    // .contactSubBtn:nth-child(2){
    //   top: 15px;
    //   left: -60px
    // }

    // .contactSubBtn:nth-child(3){
    //   top: -45px;
    //   left: -45px
    // }

    // .contactSubBtn:nth-child(4){
    //   top: -60px;
    //   left: 15px
    // }
  }
};

.contactSubBtn{
  cursor: pointer;
  position: absolute;
  top: 12px;
  left: 12px;
  z-index: -1;


  width: 50px;
  height: 50px;
  padding: 1px;

  border-radius: 50%;
  @include gradient-dark;

  &_inner{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;

    background-color: $darkMain;
    border-radius: 50%;

    transition: background-color .4s;
  }

  &_img{
    width: 25px;
    height: 25px;
  }

  &:hover{
    .contactSubBtn_inner{
      background-color: transparent
    }
  }

  &:nth-child(2){
    img{
      position: absolute;
      right: 13px;
    }
  }
}

@media (max-width: 1280px) {
  .contactBtn{
    display: none;
  }

}