@import '../../../assets/styles/variables';

.btnDown{
  margin: 0 auto;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 50px;

  cursor: pointer;
  width: 35px;
  height: 64px;
  padding: 0.5rem;
  border-color: $light;
  border-width: 4px;
  border-radius: 1.5rem;

  div{
    width: 0.75rem;
    height: 0.75rem;
    border-radius: 50%;
    background-color: $light;
    margin-bottom: 0.25rem;
  }
}

@media (max-width: 1280px){
  .btnDown{
    display: none;
  }
}